html {
  scroll-snap-type: y mandatory;
}
.app {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: rgb(90, 83, 83);
}
.app__top {
  margin-bottom: -50px;
}
.app__top > h1 {
  text-align: center;
  color: white;
}
.app__logo {
  height: 12vh;
  margin-left: 75px;
}
.app__videos {
  position: relative;
  height: 70vh;
  background-color: white;
  width: 80%;
  border-radius: 25px;
  max-width: 450px;
  max-height: 1200px;
  overflow: scroll;
  scroll-snap-type: y mandatory;
}
.app__videos::-webkit-scrollbar {
  display: none;
}
.app__videos {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
